@use '../src/sass/layout';
@use '../src/sass/query';

html {
	scroll-behavior   : smooth;
	scroll-padding-top: 8rem;

	font-family: sans-serif;
	line-height: 1.75;
}

header,
main {
	margin-inline: auto;
	width : min(calc(100% - 2vw), 48rem);
}

header {
	position  : sticky;
	top       : 0;
	z-index   : 1;
	min-height: 6rem;

	border          : 1px solid #ddd;
	background-color: rgba(#fff, 0.5);
	backdrop-filter : blur(4px);
}

main {
	margin-top: 2rem;
}

hr {
	margin-block: 2rem;
}

div.blank {
	margin-block    : 2rem;
	height          : 20vh;
	background-image: repeating-linear-gradient(-45deg, transparent, transparent 6px, #defa 6px, #defa 12px);
}

.col-2 {
	display: grid;
	gap    : 1rem;
	grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
	> * { margin: 0; }
}

.width-query,
.height-query,
.browser-query {
	padding: 0.25rem 0.5rem;
	border: 1px solid rgba(gray, 0.25);
}

div + div {
	margin-top: 1rem;
}

.width-query {
	&.phone-up {
		@include query.for-phone-up              { border: 2px solid red; }
	}
	&.phone-only {
		@include query.for-phone-only            { border: 2px solid red; }
	}
	&.phone-portrait-up {
		@include query.for-phone-portrait-up     { border: 2px solid red; }
	}
	&.phone-portrait-only {
		@include query.for-phone-portrait-only   { border: 2px solid red; }
	}
	&.phone-landscape-up {
		@include query.for-phone-landscape-up    { border: 2px solid red; }
	}
	&.phone-landscape-only {
		@include query.for-phone-landscape-only  { border: 2px solid red; }
	}
	&.tablet-up {
		@include query.for-tablet-up             { border: 2px solid red; }
	}
	&.tablet-only {
		@include query.for-tablet-only           { border: 2px solid red; }
	}
	&.tablet-portrait-up {
		@include query.for-tablet-portrait-up    { border: 2px solid red; }
	}
	&.tablet-portrait-only {
		@include query.for-tablet-portrait-only  { border: 2px solid red; }
	}
	&.tablet-landscape-up {
		@include query.for-tablet-landscape-up   { border: 2px solid red; }
	}
	&.tablet-landscape-only {
		@include query.for-tablet-landscape-only { border: 2px solid red; }
	}
	&.desktop-up {
		@include query.for-desktop-up            { border: 2px solid red; }
	}
	&.desktop-only {
		@include query.for-desktop-only          { border: 2px solid red; }
	}
	&.big-desktop-up {
		@include query.for-big-desktop-up        { border: 2px solid red; }
	}
	&.big-desktop-only {
		@include query.for-big-desktop-only      { border: 2px solid red; }
	}
}

.height-query {
	&.tiny-height-up {
		@include query.for-tiny-height-up     { border: 2px solid red; }
	}
	&.tiny-height-only {
		@include query.for-tiny-height-only   { border: 2px solid red; }
	}
	&.small-height-up {
		@include query.for-small-height-up    { border: 2px solid red; }
	}
	&.small-height-only {
		@include query.for-small-height-only  { border: 2px solid red; }
	}
	&.medium-height-up {
		@include query.for-medium-height-up   { border: 2px solid red; }
	}
	&.medium-height-only {
		@include query.for-medium-height-only { border: 2px solid red; }
	}
	&.large-height-up {
		@include query.for-large-height-up    { border: 2px solid red; }
	}
	&.large-height-only {
		@include query.for-large-height-only  { border: 2px solid red; }
	}
}

.browser-query {
	&.chrome {
		@include query.for-chrome  { border: 2px solid red; }
	}
	&.firefox {
		@include query.for-firefox { border: 2px solid red; }
	}
	&.safari {
		@include query.for-safari  { border: 2px solid red; }
	}
}


// -----------------------------------------------------------------------------


*[class ^='layout-'] {
	border: 1px dashed blue;
	> div, span {
		background-color: rgba(red, 0.5);
		border: 4px solid pink;
		margin: 0;
	}
	> span {
		display: inline-block;
	}
}

.height-spacer {
	height: 6rem;
}

.col-2 {
	display: grid;
	gap    : 2rem;
	grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
	> * { margin: 0; }
}


// -----------------------------------------------------------------------------


.layout-fill-self-wrap {
	position: relative;
}

.layout-full-width {
	@include layout.full-width(1rem);
}

.layout-centering {
	@include layout.centering;
}

.layout-centering-self {
	@include layout.centering-self(true, true, relative);
}

.layout-fill-self {
	@include layout.fill-self;
}

.layout-side-margin {
	@include layout.side-margin(40rem, 1rem);
}

.layout-aspect {
	@include layout.aspect(3, 1);
}

.layout-frame-contain {
	@include layout.frame-contain(3, 2);
}

.layout-wrap {
	@include layout.wrap((1rem 2rem));
}

.layout-column-2 {
	@include layout.column(2, 2rem);
}

.layout-column-3 {
	@include layout.column(3, 2rem);
}

.layout-column-4 {
	@include layout.column(4, 2rem);
}
